.scoreboard {
  padding: 20px;
  color: white;
  width: 40%; }
  @media (max-width: 768px) {
    .scoreboard {
      width: 85%;
      padding: 10px; } }
  @media (max-width: 480px) {
    .scoreboard {
      width: 98%;
      padding: 5px; } }
  .scoreboard .score {
    list-style: none; }



